$header-height: 72px;

.header {
  width: 100%;
  height: $header-height;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  background-color: $colour-primary;
  color: $white;

  .header__item {
    height: 100%;
    width: 144px;

    &:hover {
      background-color: $colour-accent;
      color: $black;
      box-shadow: 0 -2px 5px;
      cursor: pointer;
    }
  }

  .header_item-text {
    margin-top: 16px;
    font-size: 24px;
    text-align: center;
  }

  .header__button-container {
    width: $header-height;
    height: 100%;
  }

  .header__button-icon {
    margin: 8px;
    width: 56px;
    height: 56px;

    &:hover {
      color: $black;
    }
  }
}